import axios from 'axios';

const url = 'api/key/';

class KeyService {

    static getKeys() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(key => ({
                            ...key
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

}

export default KeyService;
