import keys from '../utils/key';
import KeyService from "../KeyService";

const CALLBACK_NAME = 'init';
const API_KEY = 'AIzaSyDs36InNFYapW1USDBHRxnoYZ0Nc4XLABA';


let initialized = !!window.google;
let resolveInitPromise;
let rejectInitPromise;


 function getKey () {

    return KeyService.getKeys()

}


const initPromise = new Promise((resolve, reject) => {
    resolveInitPromise = resolve;
    rejectInitPromise = reject;
});

export default function init(key) {





    if (initialized) return initPromise;


    // let getKey = await keys();
    // let API_KEY = getKey[0].key;
    // console.log(API_KEY);

    initialized = true;
    const script = document.createElement('script');
                script.async = true;
                script.defer = true;
                script.src = `https://maps.googleapis.com/maps/api/js?key=${key}&callback=${CALLBACK_NAME}`;
                script.onerror = rejectInitPromise;
                document.querySelector('head').appendChild(script);
    // The callback function is called by
    // the Google Maps script if it is
    // successfully loaded.
          window[CALLBACK_NAME] = () => resolveInitPromise(window.google)
          // We inject a new script tag into
          // the `<head>` of our HTML to load
          // the Google Maps script.




          return initPromise;
}
